body{font-size:14px}.slider-wrapper:after{content:"";display:table;clear:both}.slider-wrapper .slider-list-wrapper{overflow:hidden}.slider-wrapper .slider-list-wrapper .slider-list{display:none;transition:all .3s ease-in}.slider-wrapper .slider-list-wrapper .slider-list:after{content:"";display:table;clear:both}.slider-wrapper .slider-list-wrapper .slider-list .slider-item{opacity:0;float:left;background-color:#eee;transition:all .3s ease-in}.slider-wrapper .slider-indicator{margin:2rem 0;text-align:center;font-size:0}.slider-wrapper .slider-indicator .slider-indicator-item{display:inline-block;width:.5rem;height:.5rem;margin:0 .5rem;background-color:#ddd}.slider-wrapper .slider-indicator .slider-indicator-item.active{background-color:#607d8b}.slider-wrapper .slider-prev,.slider-wrapper .slider-next{cursor:pointer;text-align:center;background-color:#eee}.slider-wrapper .slider-prev:hover,.slider-wrapper .slider-next:hover{background-color:#607d8b;color:#fff}